import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home'
import personal from '../components/personal'
import publish from '../components/publish'
import tucao from '../components/tucao'
import login from  '../components/login'
Vue.use(Router)

export default new Router({

  routes: [
    {
      path:'/',
      redirect:'/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta: {
        show: true
    }
    },
    {
      path: '/personal',
      name: 'personal',
      component: personal,
      meta: {
        show: true
      }
    },
    {
      path: '/publish',
      name: 'publish',
      component: publish,
      meta: {
        show: true
      }
    },
    {
      path: '/tucao',
      name: 'tucao',
      component: tucao,
      meta: {
        show: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        show: false
      }
    }

  ]
})



